import {Component, createRef} from 'react';
import PropTypes from 'prop-types';
import {defaultProps, propTypes} from '../components/HTMLAnnotator.react';


const findSelectButtonPosition = position => {
  return position.reduce((least, item) => {
    if (item.top < least.top) {
      least = item;
    }
    return { top: least.top, left: least.left };
  }, position[0]);
};

const structureClientRectangle = (
  clientRectangleArray,
  scrollPosition
) => {
  return Array.from(clientRectangleArray).map(item => {
    return {
      left: item.left + scrollPosition.scrollX,
      top: item.top + scrollPosition.scrollY,
      height: item.height,
      width: item.width
    };
  });
};

class Annotation extends Component {
    annotationStyle(rect, { color }, hidden=false) {
        let styles = {
            position: 'absolute',
            height: rect.height + 2,
            top: rect.top - this.props.scrollPosition.scrollY,
            left: rect.left - this.props.scrollPosition.scrollX,
            width: rect.width,
            transform: 'translate(2px)',
            backgroundColor: 'none',
            opacity: 1,
        }

        if (hidden) {
            styles = {
                ...styles,
                backgroundColor: color || this.props.color || 'yellow',
                zIndex: -1
            }
        }

        return styles
    }

    render() {
        const findAnnotations = () => {
        return this.props.highlights.map(singleAnnotation => {
          return singleAnnotation.position.map((rectangle, index) => {
            return (
              <div key={index}>
                <div
                    style={this.annotationStyle(rectangle, singleAnnotation)}
                    onClick={() => this.props.onClick(singleAnnotation)}
                    onMouseOver={() => this.props.onMouseOver(singleAnnotation)}
                />
                <div style={this.annotationStyle(rectangle, singleAnnotation, true)} />
              </div>
            );
          });
        });
      };

      return <div>{findAnnotations()}</div>;
    }
}

export default class HTMLAnnotator extends Component {
    constructor(props) {
        super(props);
        this.iframeRef = createRef();
    }

    state = {
        newAnnotationJustCreated: false,
        selection: null,
        scrollPosition: {
            scrollY: 0,
            scrollX: 0
        }
    }

    componentDidMount() {
        this.removeIframeEventHandlers()
    }

    componentWillUnmount() {
        this.removeIframeEventHandlers()
    }

    removeIframeEventHandlers() {
        if (this.iframeRef?.current?.contentDocument) {
            this.iframeRef.current.contentDocument.removeEventListener(
                'scroll', this.onScroll.bind(this))
            this.iframeRef.current.contentDocument.removeEventListener(
                'mouseup', this.onMouseUp.bind(this))
        }
    }

    setIframeEventHandlers() {
        if (this.iframeRef?.current?.contentDocument) {
            this.iframeRef.current.contentDocument.addEventListener(
                'scroll', this.onScroll.bind(this))
            this.iframeRef.current.contentDocument.addEventListener(
                'mouseup', this.onMouseUp.bind(this))
        }
      }

    onScroll() {
        this.setState({
            scrollPosition: {
                scrollY: this.iframeRef.current.contentWindow.scrollY,
                scrollX: this.iframeRef.current.contentWindow.scrollX
            }
        })
    }

    onMouseUp() {
        const selection = this.iframeRef.current.contentWindow.getSelection();
        const text = selection.toString();
        if (text) {
            const clientRectangleArray = selection.getRangeAt(0).getClientRects();
            const position = structureClientRectangle(
                clientRectangleArray,
                this.state.scrollPosition
            );
            this.setState({
                selection: { position, text },
                newAnnotationJustCreated: true
            })
            this.onAnnotationCreated(this.state.selection)
            this.props.setProps({
                highlights: [
                    ...this.props.highlights,
                    this.state.selection
                ]
            })
                        
        }

        this.setState({ selection: null })
        selection.empty()
    }

    triggerEvent(name, value) {
        this.props.setProps({
            event: { name, value, _: !this.props.event?._ }
        })
    }

    onAnnotationCreated(highlight) {
        this.triggerEvent('new_highlight', highlight)
    }

    onAnnotationClick(highlight) {
        this.triggerEvent('clicked_highlight', highlight)
    }

    onAnnotationHover(highlight) {
        if (!this.state.newAnnotationJustCreated) {
            this.triggerEvent('hovered_highlight', highlight)
        } else {
            this.setState({
                newAnnotationJustCreated: false
            })
        }

    }

    render() {
      const width = this.props.width || '100%';
      const height = this.props.height || '100%';

      return (
        <div
          onMouseLeave={() => this.setState({ selection: null })}
          style={{
            overflow: 'hidden',
            position: 'relative',
            width,
            height
          }}
        >
          <iframe
            srcDoc={this.props.data}
            width={width}
            height={height}
            ref={this.iframeRef}
            onLoad={this.setIframeEventHandlers.bind(this)}
          ></iframe>
          {!!this.props.highlights && (
            <Annotation
              highlights={this.props.highlights}
              color={this.props.color}
              scrollPosition={this.state.scrollPosition}
              onClick={this.onAnnotationClick.bind(this)}
              onMouseOver={this.onAnnotationHover.bind(this)}
            />
          )}
        </div>
      );
    }
}


HTMLAnnotator.defaultProps = defaultProps;
HTMLAnnotator.propTypes = propTypes;
